.image-container{
    display:flex;


   
}
.image-container img{
    max-width: 100%;
    max-height: 100%;
    width: 100%;
}
@media (max-width: 800px){
    .image-container{
        display: none;
    }
}@media (orientation: landscape) {
    .image-container {
        display: flex;
      overflow: hidden; /* Hide content in landscape mode */
    }
  }