.main_container {
  display: flex;
  align-items: center;
  flex-wrap: wrap; /* Allow wrapping on smaller screens */
  position: relative; /* Add relative positioning */
  padding-bottom: 10rem;
  opacity: 1;
  
}

.main{
  /* background: linear-gradient(to bottom, #1c1b41, #3c3360);  */
  background-image: linear-gradient(to right, #10063a, #11073d);
background-size: 100%;


}

.text {
  flex: 1;
  text-transform: uppercase;
  margin-left: -10rem;
  /* background-image: linear-gradient(to right, #98edf5, #eceef7);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; */
  background-image: linear-gradient(to right, #8de0e7, #ffffff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Courier New';
  font-weight: 900;
  position: absolute;
  font-size: 18px;
  margin-top: 1.5rem;

}

p {
  margin-bottom: 0.5rem;
}

.text .dev {
  font-size: 10px;
}

.icons .icon {
  margin-left: 1rem;
  color: rgb(168, 224, 246);
  position: relative;
}

.icons .icon1 {
  color: rgb(168, 224, 246);
  position: relative;
}

.image {
  display: flex;
  justify-content: flex-end;
  margin-left: 10rem;
  margin-right: -5rem;
}

.image img {
  max-width: 60%;
  max-height: 20%;
  width: 20%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  margin-top: -1rem;
  margin-left: 40rem;
}

.main {
  padding: 205px; /* Adjust the top padding to create space for the header */
  position: relative; /* Add relative positioning */
  z-index: 1; /* Add a lower z-index value to keep it below the main content */
}


h1 {
  font-size: 54px; /* Adjust the font size for smaller screens */
  font-family: 'Courier New', Courier, monospace;
}

.text .dev {
  font-size: 10px;
}



.image {
  display: flex;
  justify-content: center; /* Center the image on smaller screens */
  padding: 0px;
  margin-top: 1rem;
}

.image img {
  max-width: 90%; /* Make the image responsive by adjusting the width */
  max-height: auto; /* Allow the height to adjust based on the image aspect ratio */
  width: 40%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  width: 40%
}

/* Media query for screens with a maximum width of 768px (mobile) */
@media (max-width: 768px) {
  .main {
    display: flexbox;
    justify-content: center;
    background: linear-gradient(to bottom, #070212, #0f0f23);
    padding-top: 10%;
    margin-left: -10rem;
    margin-right: 0rem;
    padding-bottom: 93%;
   
  }

  h1 {
    font-size: 20px;
    background-image: linear-gradient(to right, #8de0e7, #ffffff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .text {
    margin-left: -1.5rem;
    justify-content: center;
    font-size: 12px;
    background-image: linear-gradient(to right, #8de0e7, #ffffff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  .image {
    margin-left: 0rem;
    display: flex;
    margin-top: 10rem;
    width: 110px;
    height: 100%;
  }

  .image img {
    margin-right: 15rem;
    width: 150px;
    height: 100%;
    margin-top: -3rem;
  }
}

@media (max-width: 900px) and (orientation: landscape) {
  .main {
    overflow: hidden;
  }

  .image {
    margin-left: -13rem;
    width: 50%;
  
  }

  .image img {
    margin: auto;
    margin-top: 10rem;
    width: 100%;
    margin-left:65rem;
  }
}
