
.section2{
  display: flex;
  align-items: center;
  justify-content: center;
 
  /* background: linear-gradient(to bottom, #0c8ede, #187bb8);  */
  background-color: #0d052f;
  width: 100%;
}
.about-container {
  transform: scale(1); /* Add this line to set the initial scale */
    transition: transform 0.3s ease; /* Add transition for smooth scaling */
 
   
    justify-content: center;
}

.about-container h1 {
  margin-right: 1rem;
  background-image: linear-gradient(to right, #98edf5, #eceef7);
  -webkit-background-clip: text;
-webkit-text-fill-color: transparent;
}






.paragraph-wrapper {
  display: inline-block;
}

.paragraph-border {
  border-left: 4px solid black;
  padding-left: 1rem;
  width: 400px; /* Adjust the width to your desired size */
  height: 380px; /* Adjust the height to your desired size */
  overflow: hidden;
  border-color: rgb(213, 238, 244);
}

.paragraph-border p {
  margin: 0;
  word-wrap: break-word; /* Add this line to enable word wrapping */
  background-image: linear-gradient(to right, #ffffff, #98edf5);
  -webkit-background-clip: text;
-webkit-text-fill-color: transparent; 
font-family: 'Courier New', Courier, monospace    ;
  font-weight: bolder;
}
/* Media query for screens with a maximum width of 768px (mobile) */
@media (max-width: 768px) {
  .about-container {
    padding: 50px;
    max-width: 130%;
    width: 130%;
    overflow: hidden;
    padding-top:90px;
    padding-bottom:90px;
 
    
  }

  .paragraph-border {
    width: 284px; /* Remove the fixed width for mobile */
    height: auto; /* Remove the fixed height for mobile */
  }
  .about-container h1 {
    font-size: 34px;
  }
}
@media (orientation: landscape) {
  .section2 {
    overflow: hidden; /* Hide content in landscape mode */
  }
}