.Header {
  max-width: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  z-index: 2;
  font-family: 'Courier New', Courier, monospace;
  border-bottom: 0.0rem solid #dad0e3;
  /* background: linear-gradient(to bottom, #000000, #000000); */
  background-image: linear-gradient(to right, #f3f2fb, #f5f4ff);

  position: fixed;
 
  max-height: max-content;

}

.head {
  color: #000000;
  font-weight: 600;
  margin-top: 1rem;
  max-width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  flex-grow: 1;
  margin-left: 1rem;
}

.head h2 {
  margin-right: 44rem;
}
.head h3 {
  margin-right: 42rem;
}

.head p {
  margin-right: 5rem;
}

.head p {
  margin-right: 44rem;
}

.navbar {
  max-width: 100%;
  margin: 0 auto;
  overflow: auto;
}

ul {
  display: flex;
  text-align: right;
  justify-content: flex-end;
 
}

ul li {
  margin: 1rem;
  margin-top: 1.5rem;
  list-style: none;
  text-align: right;
}

ul li a {
  text-decoration: none;
  text-transform: uppercase;
  background-image: linear-gradient(to right, #070707, #1e1d1d);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

ul li a:hover {
  border-bottom: 3px solid rgb(172, 175, 242);
}

.changer {
  display: flex;
  margin-right: 3.5rem;
}

.changer .menu {
  display: none;
  cursor: pointer;
  margin-right: 1rem;
}

.closed {
  display: none;
}

.closed .close {
  display: none;
}

@media (max-width: 800px) {
  .Header {
    justify-content: center;
   
  }

  .navbar {
    display: flex;
    transition: ease;
    justify-content: center;
    position: relative;
   

  }
  .head{
    margin-bottom:1rem;
    padding-bottom: -1%;
  }
  .head h2 {
    margin-right: 0rem;
    margin-left: 6rem;
    font-size: 13px;
  }
  .head h3 {
    margin-right: 0rem;
   
    font-size: 13px;

  }
  

  .navbar.active {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    inset: 0;
    background: whitesmoke;
    transition: 1400ms ease;
  }

  ul {
    margin: 0;
    align-items:center;

    
    margin-top: 0rem;
    padding-bottom: 3%;
  }

  ul li {
    margin-block: 1rem;
    margin-right: 0.5rem;
    
  }

  ul li a {
    font-size: 10px;
    color: black;
    font-weight: bolder;
    flex-direction: row;
  }

  .changer .menu {
    display: block;
  }

  .closed {
    display: block;
  }

  .closed .close {
    display: block;
  }

  .Header_logo {
    margin-left: 0.450rem;
  }
}

@media (max-width: 350px) {
  ul {
    margin: 0;
    align-items: center;
    flex-direction: column;
    margin-top: 1rem;
  }

  ul li {
    margin-block: 2rem;
  }

  ul li a {
    font-size: 20px;
    color: black;
    font-weight: bolder;
  }

  .changer .menu {
    display: block;
    margin-right: 0;
  }
}

@media (max-width: 1000px) {
  .head {
    margin-right: 5%;
    margin-left: 10%;
    display: flex;
    align-items: center;
    position: relative;
  }

  .head h2 {
    margin-right: 2rem;
  }

  .head p {
    margin-right: 0;
  }
}

@media (min-resolution: 288dpi){
  .head{
    display:relative;
  }
}